.CoursesList-item {
  margin: 1rem auto 0;
  list-style-type: none;
  background-color: #FFF;
  border-radius: .5rem;
  -webkit-box-shadow: .1rem .1rem .5rem rgba(0, 0, 0, .25);
          box-shadow: .1rem .1rem .5rem rgba(0, 0, 0, .25);
}

.CoursesList-item figure{
  margin:0;
}

.CoursesList-link img {
  border-radius: .5rem .5rem 0 0;
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
  -webkit-transition: all .3s ease-out;
  -o-transition: all .3s ease-out;
  transition: all .3s ease-out;
}

.CoursesList-link:hover img { -webkit-filter: grayscale(1); filter: grayscale(1); }

.CoursesList-link h2 {
  padding: 1rem;
  font-size: 5vw;
}

.CoursesList-item p { padding: 1rem; }

.CoursesList-item .fa { padding-right: 1vw; }

.CoursesList-item b { padding-right: 5vw; }

@media screen and (min-width: 48em) {
  .CoursesList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .CoursesList-item { width: 40%; }

  .CoursesList-item h2 { font-size: 1.25rem; }
}

@media screen and (min-width: 64em) {
  .CoursesList {
    margin: auto;
    max-width: 1200px;
  }

  .CoursesList-item { width: 30%; }
}


